// export default TopicCards;

import React, { useEffect, useRef, useState } from "react";
import Card from "./card";
import "./style.css";
import FeatureCard from "./FeatureCard";
import FeatureData from "./data/FeatureData";

function TopicCards({ topic, cardDetails, id }) {
  const separatorRef = useRef(null);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (separatorRef.current) {
        const separatorTop = separatorRef.current.getBoundingClientRect().top;
        const windowHeight = window.innerHeight;

        if (separatorTop < windowHeight) {
          setIsVisible(true);
        }
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <div className="container scrolling" id="custom-container">
      <h1 style={{ textAlign: "center", padding: "22px" }}>{topic}</h1>
      <div className="row horizontal-scroll">
        <div className="scrolling-container">
          <FeatureCard
            image={FeatureData[id].imageUrl}
            heading={topic}
            description={FeatureData[id].description}
          />
          {cardDetails.map((card, index) => (
            <div key={index} className="col-lg-4" id="custom-col">
              <Card
                imageSrc={card.imageSrc}
                imageAlt={card.imageAlt}
                cardText={card.cardText}
              />
            </div>
          ))}
        </div>
      </div>
      <hr
        className={`separator separator--line ${
          isVisible ? "separator-visible" : ""
        }`}
        ref={separatorRef}
      />
    </div>
  );
}

export default TopicCards;
