import React from "react";
function FeatureCard(props) {
  const style = {
    maxWidth: "100%", // Adjust the width as needed
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
  };

  const cardStyle = {
    width: "300px",
    height:"300px",
    margin:"28px" , 
    padding:"10px",
    border:"none",
    textAlign:"center"
  };

  const imageStyle = {
    height: "45%", 
    width:"45%",
    objectFit: "cover",
    margin:"auto",
    paddingTop:"5px"
  };

  const textContainerStyle = {
    maxHeight: "33%",
    minHeight:"33%", 
    overflow: "hidden",
    marginBottom:"10px"
  };
  return (
    <div className="col-lg-4">
      <div className="card" style={cardStyle}>
        <img
          className="card-img-top rounded-circle"
          src={props.image}
          alt="Generic placeholder image"
          style={imageStyle}

        />
        <div className="card-body">
          <h5 className="card-title" id="textWrap">{props.heading}</h5>
          <p className="card-text" style={textContainerStyle}>
            {props.description}
          </p>
          <a className="btn btn-secondary" href="#" role="button">
            Learn more &raquo;
          </a>
        </div>
      </div>
    </div>
  );
}

export default FeatureCard;
