import React from "react";
import "./style.css"


function Card(props) {
    const cardStyle = {
      width: "300px",
      height:"300px",
      margin:"10px" 
    };
  
    const imageStyle = {
      height: "75%", 
      objectFit: "cover",
    };
  
    const textContainerStyle = {
      maxHeight: "33%",
      minHeight:"33%", 
      overflow: "hidden",
      marginBottom:"10px"
    };
  
    return (
      <div className="col-md-4">
        <div className="card md-4 box-shadow"  style={cardStyle}>
          <img
            src={props.imageSrc}
            className="card-img-top"
            alt={props.imageAlt}
            style={imageStyle}
          />
          <div className="card-body">
            <h5 className="card-text" style={textContainerStyle}>
              {props.cardText}
            </h5>
            <div className="d-flex justify-content-between align-items-center">
              <div className="btn-group">
               
                <a className="btn btn-secondary" href="#" role="button">
                    View details &raquo;
        </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  export default Card;
  