// Header.js
import React from "react";
import "./style.css"
import image from "../components/data/image1.jpg"


function Header() {
  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-light bg-light custom-nav-class">
        <div className="container">
          <a className="navbar-brand" href="#">
            Logo
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="true"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse justify-content-end" id="navbarNav">
            <ul className="navbar-nav ml-auto">
              <li className="nav-item active mr-5" id="home">
                <a className="nav-link" href="">
                  Home
                </a>
              </li>
              <li className="nav-item mr-5" id="posts">
                <a className="nav-link" href="">
                  Posts
                </a>
              </li>
              <li className="nav-item mr-5" id="compose">
                <a className="nav-link" href="">
                  Compose
                </a>
              </li>
              <li className="nav-item mr-5" id="about">
                <a className="nav-link" href="">
                  About
                </a>
              </li>
              <li className="nav-item mr-2" id="contact">
                <a className="nav-link" href="">
                  Contact
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Header;
